import React, { useState, useEffect } from 'react';
import { ServicesManager, CommandsManager} from '@ohif/core';
import  { Button, ButtonEnums,Label,Select,InputText,Checkbox } from '@ohif/ui'
import testData from './fhircastTestData'
import { useAppConfig } from '@state';

function FhircastPanel({
  servicesManager,
  commandsManager,
  extensionManager,
}): React.FunctionComponent { 
 

  const {FhircastService}=servicesManager.services;
  const [hubConnected, sethubConnected] = useState(false); 
  const [tokenAcquired, setTokenAcquired] = useState(false);
  const [topic,setTopic]=useState('FHIRcast conference');
  const [publishEvent,setPublishEvent]=useState({label:'ImagingStudy-open',value: 'ImagingStudy-open'});
  const [selectedHub,setSelectedHub]=useState({value:'TEST',label:'TEST-FHIRcast Javascript Sandbox'});  

  const [overwriteDataCheckbox,setOverwriteDataCheckbox]=useState(false);
  const [patientOpenCheckbox,setPatientOpenCheckbox]=useState(true);
  const [patientCloseCheckbox,setPatientCloseCheckbox]=useState(true);
  const [imagingStudyOpenCheckbox,setImagingStudyOpenCheckbox]=useState(true);
  const [imagingStudyCloseCheckbox,setImagingStudyCloseCheckbox]=useState(true);
  const [diagnosticReportOpenCheckbox,setDiagnosticReportOpenCheckbox]=useState(true);
  const [diagnosticReportCloseCheckbox,setDiagnosticReportCloseCheckbox]=useState(true);

  const [sendIMOpen,setSendIMOpen]=useState(true);
  const [sendIMClose,setSendIMClose]=useState(true);

  const [appConfig] =useAppConfig();
 
  let hub={
    name:'',
    friendlyName:'',
    productName :'',
    enabled:false,
    events: [],
    lease:999,
    hub_endpoint: '',
    authorization_endpoint: '',
    token_endpoint: '',
    token:'',
    subscribed:false,
    subscriberName:''
  };


  function getHubOptions(){   // used to fill the hub select drop down
    var options=[];
    if (appConfig.fhircast.hubs) {
      appConfig.fhircast.hubs.forEach(hub =>
      {
        if (hub.enabled) {
          options.push({label:hub.name+'-'+hub.friendlyName,value:hub.name});
        }
      })
    }
    return options;
  }

  
  const getTokenClick= async() => {
    console.debug('FhircastPanel: Getting token from the  hub: ' + FhircastService.getHub().name); 
    var succeeded= await FhircastService.getToken();
    if (succeeded) {
      setTokenAcquired(true);
      if (FhircastService.getHub.subscribed) {sethubConnected(true);}
    }
  }

  useEffect(() => {
    hub=FhircastService.getHub();
    if (hub.subscribed)  {sethubConnected(true); }
    if (hub.token != '') {setTokenAcquired(true); }
    setSelectedHub({label: hub.name,value:hub.friendlyName });
    setTopic(FhircastService.getTopic())
    // ~~ Subscription
    const fhircastEvent = FhircastService.EVENTS.FHIRCAST_MESSAGE;
    const hubSubscribed= FhircastService.EVENTS.HUB_SUBSCRIBED;
    const hubUnsubscribed= FhircastService.EVENTS.HUB_UNSUBSCRIBED;
    const websocketClose = FhircastService.EVENTS.WEBSOCKET_CLOSE;
    const subscriptions = [];
    [fhircastEvent, hubSubscribed, hubUnsubscribed,websocketClose,].forEach(evt => {
      const { unsubscribe } = FhircastService.subscribe(evt, () => {      
        if ( evt==='tokenAcquired' ) {
          setTokenAcquired(true);
          console.debug('FhircastPanel: tokenAcquired event received.');
        }
        if (evt==='hubSubscribed') {
          sethubConnected(true);
          console.debug('FhircastPanel: hubSubscribed event received.');
        }
        if (evt==='hubUnsubscribed'  ) {
          sethubConnected(false);
          console.debug('FhircastPanel: hubUnsubscribed event received.');
        }
        if ( evt==='websocketClose' ) {
          sethubConnected(false);
          console.debug('FhircastPanel: websocketClose event received.');
        }
        if (evt==='fhircastMessage') {
          console.debug('FhircastPanel: fhircastMessage event received.');
        }
      });
      subscriptions.push(unsubscribe);
    });
    return () => {
      subscriptions.forEach(unsub => {
        unsub();
      });
    };
  },[]);

  const subscribeClick= async() => {
    console.debug('FhircastPanel: Subscribing to the hub.');
    if (FhircastService.getTopic() === undefined) {
      FhircastService.setTopic(topic);
    }
    var response= await FhircastService.fhircastSubscribe();
    if (response==202) {
      sethubConnected(true);
    }
  }

  const unsubscribeClick= async() => {
    console.debug('FhircastPanel: Unsubscribing from the hub.');
    var response= await FhircastService.fhircastUnsubscribe();
    if (response==202) {
      sethubConnected(false);
    } else {
      sethubConnected(false);
    }
  }

  const publishClick= async() => {
    try {
        console.debug('FhircastPanel: calling publish');
        var eventObj=testData.filter(e=>e.label===publishEvent.label);
        if (overwriteDataCheckbox && eventObj[0].eventObject.event['hub.event'].toLowerCase()==='imagingstudy-open') {
          console.debug('FhircastPanel: Publishing ImagingStudy event from current active display port.');
          const { viewportGridService, displaySetService, cornerstoneViewportService } = servicesManager.services;
          const displaySets = displaySetService.getActiveDisplaySets();
          const displaySet = displaySets[0];
          const instance = displaySet?.instances?.[0] || displaySet?.instance;
          if (instance) {
            console.debug('FhircastPanel: Publishing ImagingStudy '+ instance.StudyInstanceUID);
            eventObj[0].eventObject.event.context[1].resource.uid='urn:oid:'+instance.StudyInstanceUID;
          }
        }
        var response=await FhircastService.fhircastPublish(eventObj[0].eventObject,); 
        console.debug('FhircastPanel: publish response: ',response.statusText);
      }
      catch (err) {
        console.warn('FhircastPanel: publish error: ',err.message);
      }
  }


  const getContextClick= async() => {
    console.debug('FhircastPanel: Fetching current context from the hub.');
    try {
      var response=await FhircastService.fhircastGetContext(); 
      console.debug('FhircastPanel: Get Context response: ',JSON.stringify(response));
      alert(JSON.stringify(response));
    } catch(err) {
      console.warn('FhircastPanel: Get Context error ',err.message);
    }
  }

  const handleChange=()=>{}
  
  const patientOpenCheckboxClick =()=>{
    setPatientOpenCheckbox(!patientOpenCheckbox);
  }
  const patientCloseCheckboxClick =()=>{
    setPatientCloseCheckbox(!patientCloseCheckbox);
  }
  const imagingStudyOpenCheckboxClick =()=>{
    setImagingStudyOpenCheckbox(!imagingStudyOpenCheckbox);
  }
  const imagingStudyCloseCheckboxClick =()=>{
    setImagingStudyCloseCheckbox(!imagingStudyCloseCheckbox);
  }
  const diagnosticReportOpenCheckboxClick =()=>{
    setDiagnosticReportOpenCheckbox(!diagnosticReportOpenCheckbox);
  }
  const diagnosticReportCloseCheckboxClick =()=>{
    setDiagnosticReportCloseCheckbox(!diagnosticReportCloseCheckbox);
  }

  const overwriteDataCheckboxClick =()=>{
    setOverwriteDataCheckbox(!overwriteDataCheckbox);
  }
  const sendIMOpenCheckboxClick =()=>{
    setSendIMOpen(!sendIMOpen);
  }
  const sendIMCloseCheckboxClick =()=>{
    setSendIMClose(!sendIMClose);
  }

  const changeHub=(selected)=>{
    setSelectedHub(selected);
    sethubConnected(false);
    setTokenAcquired(false);
    FhircastService.setHub(selected.value);
  }
  return (
    <>
      <div className="bg-secondary-main flex justify-between px-2 py-1">
        <span className="text-base center font-bold uppercase tracking-widest text-white">FHIRcast</span>     
      </div>
      <div className="ohif-scrollbar  overflow-y-auto overflow-x-hidden text-primary-light  text-base"> 
      <hr/>
      <label className="leading-[1.2] text-[12px]">Hub status: </label> 
      {hubConnected && <label className=" leading-[1.2] text-white">SUBSCRIBED</label>}
      {!hubConnected && <label className=" leading-[1.2] text-white">NOT SUBSCRIBED</label>}       
      <br/>
      <Select 
        id='hub'
        options={getHubOptions()}
        value={selectedHub}
        onChange={value => changeHub(value)}
        />
      <Button 
        type={ButtonEnums.type.secondary}
        disabled={false}
        onClick= {getTokenClick}
        size='small'
        >Get token
      </Button>
      <br/><hr/>
      <Label className=" leading-[1.2] text-primary-light " >Subscribe to a topic:
        <InputText 
          label=''
          id='topic' 
          disabled={hubConnected || !tokenAcquired}
          value={topic}
          onChange={newValue=> {  
              setTopic(newValue); 
              FhircastService.setTopic(newValue);
              }}>
        </InputText>
      </Label>
      <br/>
      <Button 
        type={ButtonEnums.type.secondary}
        disabled={hubConnected || !tokenAcquired}
        onClick= {subscribeClick}
        size='small'
        >Subscribe
      </Button>
      <Button 
        type={ButtonEnums.type.secondary}
        disabled={!hubConnected || !tokenAcquired}
        onClick= {unsubscribeClick}
        size='small'
        >Unsubscribe
      </Button>
      <br/><hr/>
      <Button 
      type={ButtonEnums.type.secondary}
      disabled={!tokenAcquired}
      onClick= {getContextClick}
      size='small'    
      >Get context
      </Button>
      <label>Auto-load context:
        <input type='checkbox' checked={!patientOpenCheckbox} onChange={patientOpenCheckboxClick}/>    
      </label>
      <br/><hr/>
      <p>Publish an event:</p>
      <Select 
        id='eventSelect'
        options={testData}
        value={publishEvent}
        onChange={value => {setPublishEvent({label: value.value});}}
        defaultValue=  {{label:'ImagingStudy-open',value: 'ImagingStudy-open'}}
      />    
      <Button 
        type={ButtonEnums.type.secondary}
        disabled={!tokenAcquired}
        onClick= {publishClick}
        size='small'  
      >Publish
      </Button>
      <label>Overwrite test data:
        <input type='checkbox' checked={overwriteDataCheckbox} onChange={overwriteDataCheckboxClick}/>    
      </label>
      <br/><hr/>
      <p>Handle events from this hub:</p>

       <br/>
      <label>Patient-open:
        <input type='checkbox' checked={patientOpenCheckbox} onChange={patientOpenCheckboxClick}/>    
      </label><br/>
      <label>Patient-close:
        <input type='checkbox' checked={patientCloseCheckbox} onChange={patientCloseCheckboxClick}/>    
      </label><br/>
      <label>ImagingStudy-open:
        <input type='checkbox' checked={imagingStudyOpenCheckbox} onChange={imagingStudyOpenCheckboxClick}/>    
      </label><br/>
      <label>ImagingStudy-close:
        <input type='checkbox' checked={imagingStudyCloseCheckbox} onChange={imagingStudyCloseCheckboxClick}/>    
      </label><br/>
      <label>DiagnosticReport-open:
        <input type='checkbox' checked={diagnosticReportOpenCheckbox} onChange={diagnosticReportOpenCheckboxClick}/>    
      </label><br/>
      <label>DiagnosticReport-close:
        <input type='checkbox' checked={diagnosticReportOpenCheckbox} onChange={diagnosticReportOpenCheckboxClick}/>       
      </label><br/>
      <br/><hr/>
      <p>Send events to this hub:</p>
      <br/>
      <label>ImagingStudy-open:
        <input type='checkbox' checked={sendIMOpen} onChange={sendIMOpenCheckboxClick} />    
      </label><br/>
      <label>ImagingStudy-close:
        <input type='checkbox' checked={sendIMClose} onChange={sendIMCloseCheckboxClick} />   
      </label><br/>

    </div>
    </>
  );

}
//setSendIMOpen(!sendIMOpen) 
 //setSendIMClose(!sendIMClose)
export default FhircastPanel;
