import React from 'react';
import { ServicesManager, CommandsManager, ExtensionManager } from '@ohif/core';
import { useViewportGrid } from '@ohif/ui';
import { MeasurementTable } from '@ohif/ui';
import FhircastPanel from './FhircastPanel';
import PowercastPanel from './PowercastPanel';


export default function getPanelModule({
  commandsManager,
  extensionManager,
  servicesManager,
}: {
  servicesManager: ServicesManager;
  commandsManager: CommandsManager;
  extensionManager: ExtensionManager;
}) {

  const wrappedPowercastPanel = () => {
    const [{ activeViewportId, viewports }] = useViewportGrid();

    return (
      <PowercastPanel
        viewports={viewports}
        activeViewportId={activeViewportId}
        onSaveComplete={() => {}}
        onRejectComplete={() => {}}
        commandsManager={commandsManager}
        servicesManager={servicesManager}
        extensionManager={extensionManager}
      />
    );
  };


  const wrappedFhircastPanel = () => {
    const [{ activeViewportId, viewports }] = useViewportGrid();

    return (
      <FhircastPanel
        viewports={viewports}
        activeViewportId={activeViewportId}
        onSaveComplete={() => {}}
        onRejectComplete={() => {}}
        commandsManager={commandsManager}
        servicesManager={servicesManager}
        extensionManager={extensionManager}
      />
      
    );
  };



  return [
    {
      name: 'PowercastPanel',
      iconName: 'power-off',
      iconLabel: 'power-off',
      label: 'Powercast',
      secondaryLabel: 'Measurements',
      component: wrappedPowercastPanel,
    },
    {
      name: 'FhircastPanel',
      iconName: 'power-off',
      iconLabel: 'power-off',
      label: 'FHIRcast',
      secondaryLabel: 'Measurements',
      component: wrappedFhircastPanel,
    },

  ];
}
