import React, { useState, useEffect } from 'react';
import { ServicesManager, ExtensionManager, CommandsManager, DicomMetadataStore } from '@ohif/core';
import  { Button, ButtonEnums,Label,Select,InputText } from '@ohif/ui'
import {powercastGetConfiguration} from './powercast'
import { useAppConfig } from '@state';

function PowercastPanel(){ 

  const [configuration, setconfiguration] = useState('');
  const [authorization, setWauthorization] = useState('');
  const [appConfig] =useAppConfig();

    const getConfigurationClick= async() => {
        console.debug('Powercast: Get topic and configuration.');
        try {
          var response=await powercastGetConfiguration(appConfig.powercastConnector[0].connector_config_endpoint); 
          console.debug('PowerCast: Get configuration response: ',JSON.stringify(response));
          setconfiguration(JSON.stringify(response));
        } catch(err) {     
          console.debug('PowerCast: Get configuration error: ',err.message);
          setconfiguration(err.message);
        }
    }


    const getAuthorizationClick= async() => {
        console.debug('Powercast: Getting auth.');
        try {
   //       const myhub=getHub();
     //     var response=await powercastGetContext(myhub,topic); 
     //     console.debug('FHIRcast: Get Context response: ',JSON.stringify(response));
            // setconfiguration(JSON.stringify(response));
            setconfiguration('"authorization_endpoint": "https://nuance-auth0-server/oauth/authorize"');
   //       alert(JSON.stringify(response));
        } catch(err) {
          
  
        }
    }

    return (
    <>
      <div className="ohif-scrollbar  overflow-y-auto overflow-x-hidden text-primary-light  text-base"> 
      <Button 
        type={ButtonEnums.type.secondary}
        onClick= {getConfigurationClick}
        size='small'
        >Get configuration
      </Button>
      <textarea className="text-[10px] text-black"
        id='configuration' 
        readOnly
        value={configuration}  
        rows={6} 
        cols={40} 
      />
      <br/><hr/>
      <Button 
        type={ButtonEnums.type.secondary}
        onClick= {getAuthorizationClick}
        size='small'    
        >Get Authorization
      </Button>
      <textarea className="text-[10px] text-black"
        id='authorization' 
        readOnly
        value={authorization}  
        rows={6} 
        cols={40} 
        />
        </div>
    </>
  );
}

export default PowercastPanel;