
export async function powercastGetConfiguration(connector_endpoint) {
    const requestOptions = {
        method: 'GET',
        headers: { 'Content-Type': 'application/json' },
    };
    try {
        const response = await fetch(connector_endpoint,requestOptions);
        if (response.status==200) {
            const config= await response.json();
            return(config); 
        }
    }
    catch(err) {
        console.debug('PowerCast: Error getting configuration.', err.message);
    }
}
export async function powercastGetToken(hub ,topic, eventCallback) {
    const endpoint='1231234414';
   // Create a URL object with the current location
    const urlObj = new URL(window.location.origin + window.location.pathname + location.search+'/fhircastCallback');
    console.debug(URL.url);
    var formData  ='hub.callback=http://localhost:3000/fhircastCallback';
    formData +='&hub.mode=subscribe' ;
    formData +='&hub.events='   +hub.events;
    formData +='&hub.secret='   +hub.secret;
    formData +='&hub.topic=' + topic   ;
    formData +='&hub.lease='    +hub.lease;
    formData +='&hub.channel.type=websocket';
    formData +='&hub.channel.endpoint='+endpoint;
    const requestOptions = {
        method: 'POST',
        headers: { 'Content-Type': 'application/x-www-form-urlencoded' },
        body: formData
    };
    try {
        const response = await fetch(hub.url,requestOptions);
        if (response.status==202) {
            console.debug('FHIRcast: Subscription accepted by hub. opening websocket.');
            //  open websocket
            var protocol='ws:';
            const url= new URL(hub.url);
            if (hub.url.includes('https:')) { protocol='wss:';}
            const wsEvents = new WebSocket(protocol+'//' + url.host + '/bind/'+endpoint);
            wsEvents.onopen = function () {
                console.debug('FHIRcast: websocket is connected.');
                //wsEvents.send('bind:'+hubs[0].endpoint);  ?? Nuance wants the endpoint back after connection I think      
            }       
            wsEvents.onmessage = function (ev) {
                console.debug('FHIRcast: websocket received data.');
                eventCallback(ev.data);
            }
            wsEvents.onclose = function () {
                console.debug('FHIRcast: websocket is closed.');
                // set status of hub to disconnected here
            }
            var subscribedHub=new fhircastHubClass();
            subscribedHub.url=hub.url;
            subscribedHub.topic=topic;
            subscribedHub.name=hub.name;
            subscribedHub.status="connected";
            return subscribedHub;
        }
        else {
            console.debug('FHIRcast: Subscription rejected by hub:',response.status);
        }
    }
    catch(err) {
        console.debug('FHIRcast: Error subscribing to the hub.', err.message);
    }
}